/** Import here yours css file here */

@import url('https://fonts.googleapis.com/css?family=Montserrat:200,300,400,600');
@import './lit.css';

*::selection{
  background-color: var(--primary-color);
  color: black;
}

html,
body,
.site {
  height: 100%;
  color: #CCC;
  font-weight: 300;
}

body{
  position: relative;
}

img {
  max-width: 100%;
}

.site {
  display: flex;
  width: 100%;
  min-height: 100%;
  overflow: auto;
}

header {
  background-color: white;
  flex: 0 0 auto;
  width: 10%;
  padding: 2em 1.5rem;
}

.horizontal {
  display: none;
}

.main {
  display: block;
  flex: 1 1 auto;
  width: 100%;
  min-height: 100%;
  overflow: auto;
  background: linear-gradient(to bottom, #000000 0%, #444 100%);
  padding-top: 67px;
  position: relative;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  vertical-align: middle;
  border-bottom: solid 0.25px rgba(255, 255, 255, 0.25);
  position: fixed;
  background: black;
  top: 0;
  width: 90%;
  z-index: 30;
}

.nav__item {
  font-size: 10px;
  text-transform: uppercase;
  margin-right: 25px;
  color: white;
  letter-spacing: 1.12px;
  font-weight: 600;
}

.nav__item:hover{
  color: var(--primary-color);
}

.cta {
  flex: 0 0 auto;
  letter-spacing: 1.12px;
  font-weight: 600;
  font-size: 10px;
}

.anchor{
  display: block;
  position: relative;
  top: -68px;
  visibility: hidden;
  pointer-events: none;
}

.row {
  border-bottom: solid 0.25px rgba(255, 255, 255, 0.25);
}

.large {
  height: 100vh;
}

.title {
  font-size: 10px;
  text-transform: uppercase;
  color: var(--primary-color);
  font-weight: 600;
  letter-spacing: 1.12px;
  display: block;
  margin-bottom: 1rem;
}
.embed {
  display: block;
}
.founders .message{
  padding-left: 0;
  display: block;
  font-size: 14px;
  letter-spacing: 1.1px;
}
.founders .message h3{
  padding: 2em 0 1em;
  color: white;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 1px;
  line-height: 1.5em;
}
.founders .message h3 a{
  color: white;
}
.founders .message .title {
  color: var(--primary-color);
  /* margin-bottom: 4rem; */
}

.founders .message .larger{
  font-weight: 300;
  font-size: 18px;
}

.venue{
  text-align: center;
  padding: 2rem;
}

.venue img{
  flex: 0 0 auto;
  margin-bottom: 2rem;
  clear: both;
}

.message {
  font-size: 22px;
  font-weight: 200;
  padding: 4rem 0 4rem 2rem;
  max-width: 800px;
  display: flex;
  align-items: center;
  line-height: 1.5em;
}

.message span {
  color: white;
  font-weight: 300;
}

.tickets {
  display: flex;
}

.tickets .right,
.tickets .left {
  width: 50%;
  padding: 2rem;
}

.tickets .left {
  border-right: solid 0.25px rgba(255, 255, 255, 0.25);
  display: flex;
  align-items: center;
  justify-content: center;
}

.coin{
  display: block;
  width: 50%;
  transform: translateY(0);
  animation: floating 10s ease-in-out infinite;
  z-index: -1;
}

.tickets .right {
  display: block;
  overflow: auto;
}

.new-page-body h3 {
  display: none;
}

.founders {
  padding: 2rem;
  background-color: rgba(87, 158, 214, 0.05);
}

.video {
  display: block;
  width: 100%;
  background-color: pink;
  position: relative;
  display: block;
  height: 0;
  padding: 0;
  overflow: hidden;
  padding-bottom: 56.25%;
}

/* Navigation Toggle */

#toggle {
  width: 2em;
  height: 2em;
  display: none;
}

#toggle:checked~.nav__links {
  visibility: visible;
  pointer-events: auto;
  opacity: 1;
  transform: translateY(0);
}

#toggle:checked~#toggle__button span {
  visibility: hidden;
}

#toggle:checked~#toggle__button span::before {
  visibility: visible;
  transform: rotate(45deg) translate(8.5px, 8.5px);
  background-color: white;
}

#toggle:checked~#toggle__button span::after {
  visibility: visible;
  transform: rotate(-45deg) translate(8.5px, -8.5px);
  background-color: white;
}

#toggle__button {
  display: none;
  position: relative;
  width: 32px;
  height: 32px;
  cursor: pointer;
}

#toggle__button span {
  background-color: white;
  display: block;
  height: 2px;
  position: relative;
  transition: all 0.1s ease;
  width: 32px;
  transform-origin: center;
  top: 50%;
  margin-top: -1px;
}

#toggle__button span::before,
#toggle__button span::after {
  background-color: white;
  content: '';
  height: 2px;
  position: absolute;
  transition: all 0.3s ease;
  width: 32px;
}

#toggle__button span::before {
  top: -12px;
  visibility: visible;
  transform-origin: center;
}

#toggle__button span::after {
  top: 12px;
  transform-origin: center;
}

html{
  background-color: black;
}

.loader{
  position: absolute;
  display: block;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: black;
  z-index: 50;
  animation: load 1s 1s ease-in-out forwards;
  pointer-events: none;
}

@media screen and (max-width: 800px) {
  .site {
    flex-direction: column;
  }
  .main {
    flex: 1 1 auto;
    -webkit-overflow-scrolling: touch;
    padding-top: 0;
  }
  .vertical {
    display: none;
  }
  .horizontal {
    display: block;
  }
  .message {
    font-size: 21px;
    padding: 2rem 1rem;
  }
  .founders {
    padding: 2rem 1rem;
    border-bottom: none !important;
  }
  .tickets {
    flex-direction: column;
    padding: none;
    height: auto;
  }
  .tickets .left,
  .tickets .right {
    width: 100%;
    padding: 2rem 1rem;
  }
  .tickets .left {
    border-right: none;
    border-bottom: solid 0.25px rgba(255, 255, 255, 0.25);
  }
  .tickets .right{
    min-height: 60vh;
  }
  .embed {
    height: 200px;
  }
  .logo__mobile{
    display: block;
    padding: 1rem;
    background-color: white;
    width: 100%;
    margin-top: 67px;
  }
  header{
    display: none;
  }
  nav {
    position: fixed;
    justify-content: space-between;
    width: 100%;
    padding: 1rem;
    z-index: 30;
  }
  .nav__links {
    text-align: center;
    width: 100%;
  }
  .nav__item {
    width: 100%;
    display: block;
    padding: 2em;
    margin-right: 0;
    border-bottom: 0.5px solid rgba(255, 255, 255, 0.1);
  }
  #toggle__button {
    display: flex;
  }
  .nav__links {
    visibility: hidden;
    pointer-events: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #000;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
    transition: all .3s ease;
    transform: translateY(-20px);
    opacity: 0;
  }
  .founders .message{
    font-size: 16px;
  }
  html,
  body,
  .site{
    font-weight: 400;
  }
}


/* Animations */

@keyframes load{
  from{
    opacity: 1;
  }
  to{
    opacity: 0;
  }
}

@keyframes floating {
  0%{
    transform: rotateY(0);
  }
  25% {
    transform: rotateY(30deg);
  }
  50% {
    transform: rotateY(0);
  }
  75% {
    transform: rotateY(-30deg);
  }
  100% {
    transform: rotateY(0)
  }
}