
:root {
  --font: Montserrat;
  --primary-color: #579ED6;
  --light: #eee;
}

*+* {
  box-sizing: border-box;
  margin: 0;
}

*{
  box-sizing: border-box;
}

body {
  font: 1em/1.6 var(--font);
}

@media(min-width:35em) {
  .col {
    display: table-cell;
  }
  .\31 {
    width: 5%;
  }
  .\33 {
    width: 22%;
  }
  .\34 {
    width: 30%;
  }
  .\35 {
    width: 40%;
  }
  .\32 {
    width: 15%;
  }
  .row {
    display: table;
  }
}

.w-100,
.row {
  width: 100%;
}

.card:focus,
hr {
  outline: 0;
  border: solid var(--primary-color);
}

.card,
pre {
  padding: 1em;
  border: solid var(--light);
}

a:hover{
  color: white;
}
.btn:hover {
  color: black !important;
  background: white !important;
  border: solid white !important;
  box-shadow: 0 0 30px 0 rgba(255,255,255,0.5) !important;
}

input.text,
input[type="text"],
input[type="button"],
input[type="submit"],
.input-checkbox {
  -webkit-appearance: none;
  border-radius: 0;
}

.c {
  max-width: 60em;
  padding: 1em;
  margin: auto;
  font: 1em/1.6 var(--font);
}

h6 {
  font: 300 1em var(--font);
}

h5 {
  font: 300 1.2em var(--font);
}

h3 {
  font: 300 2em var(--font);
}

h4 {
  font: 300 1.5em var(--font);
}

h2 {
  font: 300 2.2em var(--font);
}

h1 {
  font: 300 2.5em var(--font);
}

a {
  color: var(--primary-color);
  text-decoration: none;
  transition: all .3s ease-in-out;
}

.btn.primary {
  color: white;
  background: var(--primary-color);
  border: solid var(--primary-color);
  transition: all .3s ease-in-out;
}

pre {
  overflow: auto;
}

td,
th {
  padding: 1em;
  text-align: left;
  border-bottom: solid var(--light);
}

.btn {
  padding: 1em;
  letter-spacing: .1em;
  text-transform: uppercase;
  background: white;
  border: solid;
  font: .7em var(--font);
  border-radius: 4px;
}
